import { useState } from 'react';
import './list.css'
import Listitems from './Listitems'
const List=()=>{
    const[additem,setadditem]=useState('');
    const[items,setitems]=useState([]); // we initiliase items with an empty array i.e. we make it an array.

    const additems = (e) =>{
        setadditem(e.target.value);
    }

    const getitems = () =>{
        setitems((oldvalues)=>{  //oldvalue contains the previous state
            return [...oldvalues,additem]; // here we are returning the array value to the item array uisng spread operator
            });
            setadditem(''); // this will remove the text in the textbox after pressing add button
        };

    const deleteitems=(id)=>{
        //alert("yes");
        setitems((oldvalues)=>{
            return oldvalues.filter((arritem,index)=>{  //Filtering in React is pretty much what it says it is.
                return index !== id;                    //It’s the process of looping through an array and including or excluding elements inside that array based on a condition that you provide.
            });                                         //The caveat here is that we’re not actually filtering using React.
        })                                              // Filter is a JavaScript function that we can perform on an array type object. Remember, we use JavaScript to write React.
    }                                                   // None of these methods are special to React. React is just the UI library
                                                        
    return(
     <>
     <div className="mybody">
         <div className="center">
             <h1>LIST</h1>
             <br/>
             <input className="listinput" type = "text" name="list" placeholder="add in list" value={additem} onChange={additems}/>
             <button className="btnadd" onClick={getitems} title="Add Task">ADD</button>
             <br/>
             <hr/>
             <ol>
                {items.map((val,index)=>{   //map method of the array can have 4 attrbs. i.e. current value, index no. arr name, this.
                     return(
                         <Listitems
                          key={index}
                          id = {index}
                          items={val}
                          onSelect={deleteitems}/>
                     )
                  })}
            </ol>             
         </div>
     </div>
     </>
    );
}
export default List;