import { useState, useMemo } from "react";



const Usememo=()=>{

    const [count, setCount] = useState(10);

    const ecount = useMemo(() => {
        return count**3;
    }, [count]) // recompute when count changes.

    return(
        <>
        <div className="mybody">
            <h1>This is useMemo hook</h1>
            <hr/>
            <h2>Click button to increment by 1 : {count}</h2>
            <button className="btn" onClick={()=>setCount(count=>count+1)}> INCREASE </button>
            <h3>useMemo effect on Count is : {ecount}</h3>
        </div>
        </>
    )
}
 export default Usememo;
