import React from 'react';
import '../../index.css'

function Sum(a,b){
    var s = a+b;
    return s;
}
function Diff(a,b){
    var df = a-b;
    return df;
}
function Mul(a,b){
    var m = a*b;
    return m;
}
function Div(a,b){
    var d = a/b;
    return d.toFixed(2);
}

// const Calculation=()=>{
