
import '../../index.css';

const date = new Date();
const hrs = date.getHours();
var wish = '';
const sty = {};
if(hrs > 1 && hrs < 12)
{
    wish = 'Good Morning';
    sty.color = 'green';
}
else if(hrs >= 12 && hrs <= 19)
{
    wish = 'Good Afternoon';
    sty.color = 'orange';
}
else{
    wish = 'Good Evening';
    sty.color = 'brown';
}

const Greeting=()=>{
    return(
        <div className="greeting">
        <h2 className="text" style={sty}>{wish}</h2>
        </div>
    )
}

export default Greeting;