import { useState, useEffect } from "react";


const UseEffecthook=()=>{
    const [count,setcount] = useState(0);
    //const [count1,setcount1] = useState(0);

    useEffect(()=>{
        document.title=`You Clicked ${count} Times.`
    },[count]);
    return(
        <div className="mybody">
        <button className="btn btn-primary" style={{fontSize:"18px"}} onClick={()=> setcount(count+1)}>Click Here {count}</button>
        <br/>
        {/* <button className="btn btn-primary" style={{fontSize:"18px"}} onClick={()=> setcount1(count1+1)}>Click Here {count1}</button> */}
        </div>
    )
}
export default UseEffecthook;