import React from 'react';
import 'bootstrap/dist/css/bootstrap.css';
import Singlepost from './Singlepost';
import Postdata from './Postdata';



function spost(val){
    return(
        <Singlepost
            key={val.id}
            imgsrc= {require('./img/img1.png')}
            title={val.title}
            link={val.link}
        />
    )
}
const Props=()=>{
    return(
        <section style={{padding:'10px', margin:'5px 10px'}}>
             <div className="Container">
                 <div className="col-12">
                 <div className="row">
                {Postdata.map(spost)}     {/*postdata is an array where properties are stored */}
                 {/* OR */}
                 {/* postdata.map((val) => {
                    return(
                        <Singlepost
                            key={val.id}
                            imgsrc={val.imgsrc}
                            title={val.title}
                            link={val.link}
                        />
                    );
                 }) */}


                 {/* <Singlepost 
                 imgsrc={require('../img/img2.png')}
                 title="2nd Title"
                 link="2nd Link"
                 />
                 <Singlepost 
                 imgsrc={require('../img/img2.png')}
                 title="2nd Title"
                 link="2nd Link"
                 />
                 <Singlepost 
                 imgsrc={require('../img/img3.png')}
                 title="3rd Title"
                 link="3rd Link"
                 />
                 <Singlepost
                 imgsrc={require('../img/img1.png')}
                 title="4th Title"
                 link="4th Link"
                 /> */}
                 </div>
                 </div>
             </div>
         </section>
    )
}

export default Props;

