import React from 'react'

const Match=(prop)=>{
    return(
        <div>
            <h1>{prop.s} {prop.a} {prop.d}</h1>
            <h1>This is matching</h1>
            <hr/>
        </div>
    )
}
const Unmatch=(prop)=>{
    return(
        <div>
            <h1>{prop.s} {prop.a} {prop.d}</h1>
            <h1>This is not matching</h1>
            <hr/>
        </div>
    )
}
const Mygame = (props) => {

    let s = props.s;
    let a = props.a;
    let d = props.d;
    
    const final =  ( (s===a) && (a===d) ) ? <Match s= {props.s} a= {props.a} d= {props.d}/> : <Unmatch s= {props.s} a= {props.a} d= {props.d}/>;
    return final;
   
}

const Game=()=>{
    return(
        <>
        <div className="mybody">
        <h1 className='text'>My New Game</h1>
        <hr/>
        <hr/>
        <Mygame s= '😄' a= '😠' d= '🎅'/>
        <Mygame s= '🍎' a= '🍎' d= '🍎'/>
        <Mygame s = '🐈' a= '🐕' d= '🐀'/>
        </div>

        </>
    );
}

export default Game;