import { useState } from "react";

const SimpleF=()=>{

    const [name, setname] = useState();
    const [myentry, setmyentry] = useState();

    const updatename=(e)=>{
        //console.log(e.target.value);
        //const n = e.target.value;
        setname(e.target.value);
    }
    const onsubmit=()=>{
        setmyentry(name);
    }
    return(
        <>
        <div className="mybody">
            <h1>This is a simple input field</h1>
            <hr/>
            <h2>My Entry : {myentry}</h2>
            <input type="text" name="name1" placeholder="Enter Text Here" onChange={updatename} value={name}></input><br/>
            <button className="btn" onClick={onsubmit}>Click..!</button>
        </div>
        </>
    )
}
export default SimpleF;