import { useEffect } from 'react';
import { useState } from 'react';
import { CountdownCircleTimer } from 'react-countdown-circle-timer'

const name = 'Virender';
const web = 'https://www.w3schools.com/tags/ref_standardattributes.asp'

const Home = () =>{

let [pcount, Setpcount] = useState(localStorage.getItem('mycount'));
let pageno = parseInt(pcount);
    //let _mycount =parseInt(localStorage.getItem('mycount'));
    useEffect(() => {
      if (isNaN(pageno)) {
        pageno = 0 ;
    }   
    Setpcount(() => pageno  = pageno + 1);
    localStorage.setItem('mycount', pageno);
    }, []);

    return(
      <>
      <div className='mybody text'>
      <h1 className='text' draggable='true' style={{ color: 'blue' }}>Lets See what will happen Next</h1>
      <h2 className='text' style={{ color: 'brown' }}>this is h2 tag</h2>
      <h3 className='text' style={{ color: 'green' }}>This is from the variable  {name}</h3>
      <div className='container'>
        <div className='col-lg-12'>
          <div className='row'>
            <div className='col-lg-6 col-md-6 col-xs-12'>
              <div className='row'>
                <div className='col-lg-4'>
                  <img title='no title' src={require('../img/img1.png')} alt='myimage1' style={{ width: '100px' }} />
                </div>
                <div className='col-lg-4'>
                  <img title='no title' src={require('../img/img2.png')} alt='myimage2' style={{ width: '100px' }} />
                </div>
                <div className='col-lg-4'>
                  <a href={web} Target='_blank'><img title='link' src={require('../img/img3.png')} alt='myimage3' style={{ width: '100px' }} /></a>
                </div>
              </div>
            </div>
  
            <div className='col-lg-6 col-md-6 col-xs-12'>
              <div className='row'>
                <div className='col-lg-4'>
                  <img title='no title' src={require('../img/img1.png')} alt='myimage1' style={{ width: '100px' }} />
                </div>
                <div className='col-lg-4'>
                  <img title='no title' src={require('../img/img2.png')} alt='myimage2' style={{ width: '100px' }} />
                </div>
                <div className='col-lg-4'>
                  <a href={web} Target='_blank'><img title='link' src={require('../img/img3.png')} alt='myimage3' style={{ width: '100px' }} /></a>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div style={{position:"relative"}} >
      <h3 style={{padding:"10px",position:"fixed",bottom:"10px",right:"15px",textAlign:"right",backgroundColor:"blue",color:"white"}} >Page Count : {pcount}</h3>
    </div>
    </>
    );
}

export default Home;