import { useState } from "react";
import AddCircleIcon from '@mui/icons-material/AddCircle';
import RemoveCircleIcon from '@mui/icons-material/RemoveCircle';
import Button from '@material-ui/core/Button'
import ToolTip from '@material-ui/core/Tooltip'
const Incdec=()=>{

    const [count,setcount] = useState(0);

    const increase=()=>{
        setcount(count+1);
    }
    const decrease=()=>{
        if(count>0)
        {
            setcount(count-1);
        }
        else{
            alert("Cannot be less than 🎃");
        }
    }
    return(
        <>
        <div className="mybody" style={{fontSize:"50px"}}>
        <div className="center_div">
            <h1>Material UI</h1>
            <h1 style={{fontSize:"50px",marginTop:"20px", padding:"20px", textAlign:"center"}}>{count}</h1>
            <div style={{textAlign:"center"}}>
            <ToolTip title="Increase">
            <Button color="initial" variant="contained" size="large" style={{margin:"0px 40px"}} onClick={increase}>
            <AddCircleIcon fontSize='large'/>
            </Button>
            </ToolTip>
            <ToolTip title="Decrease">
            <Button color="initial" variant="contained" size="large" style={{margin:"0px 40px"}} onClick={decrease}>
            <RemoveCircleIcon fontSize='large'/>
            </Button>
            </ToolTip>
            </div>
        </div>
        </div>
        </>
    )
}
export default Incdec;