const Singlepost=(props)=>{
    //console.log(props);
    return(
        <>
        <div className="col-lg-3 col-md-3 col-sm-3 col-xs-6">
                    <div className="single-p">
                        <div className="p-image">
                            <img src={props.imgsrc} alt="P-img" />
                        </div>
                        <div className="p-title">
                            <h4>{props.title}</h4>
                            <p>{props.link}</p>
                        </div>
                    </div>
                </div>
                </>
    );
}

export default Singlepost;