import { useLocation, useParams, useNavigate } from "react-router-dom"
const Contact=()=>{
    const { fname } = useParams(); // we can pass multiple parameters via url and import them here.

    const location = useLocation();
    console.log(location);
    const history = useNavigate(); //previously used as useHistory()
    console.log(history);

    return(
<>
<div className="mybody">
<h1>Hi this is contact page with params {fname}</h1>
<br/>
<br/>
<h1>This is my Current Address - {location.pathname}</h1>
<br/><br/><br/>
{location.pathname === `/contact/default` ? 
(<button className="btn btn-primary" style={{fontSize:"22px"}} onClick={()=>history(-1)}>
    Go Back</button>) : null }
</div>


        </>
    )
}
export default Contact;