import { useState, useEffect } from "react";

const Useeffect=()=>{
    let date = new Date().toLocaleTimeString();

    const Updatetime=()=>{
        let time = new Date().toLocaleTimeString();
        Setctime(time);
    }
    setInterval(Updatetime,1000);

    const [Count, Setcount] = useState(0);
    
    const [Cal, Setcal] = useState(0);
    const [ctime,Setctime] = useState(date);

    useEffect(()=>{
        Setcal(() => Count * 5);
    },[Count]);    // this will work after we press the increase button i.e when Count value changes


    // useEffect(() => {
    //     let timer = setTimeout(() => {
    //     Setcount(count => count + 1);
    //   }, 1000);
    //   return () => clearTimeout(timer)
    //   }, []);
 
    
    // useEffect(()=>{
    //     setTimeout(()=>{
    //         Setcount((Count)=>Count+1)},
    //         1000);
    //     });
    return(
        <>
        {/* <div style={{padding:"10px"}}> 
            <h3>Page Count : {pcount}</h3>
        </div> */}
        <div className="mybody">
        <h1><span style={{color:"red", fontSize:"30px"}}>{Count}</span> times I die..!</h1>
        <buttton className="btn" onClick={() => Setcount(c=> c + 1 )}><span style={{fontSize:"25px"}}>Increase</span></buttton>
        <h2>After that : <span style={{color:"red", fontSize:"30px"}}>{Cal}</span></h2>
        <hr/><hr/>
        <h2>Time : <span style={{color:"red", fontSize:"30px"}}>{ctime}</span></h2>
        </div>
        </>
    );
}

export default Useeffect;