import { useState } from "react";
import Find from "./Find";

const Search=()=>{

    const [ entry , setentry] = useState("");
    
    const handlechange=(e)=>{
        const val = e.target.value;
        setentry(val);
        //console.log(val);
    }

    return(
        <>
        <div className="mybody">
        <h1>Type in the Box to change the Images</h1>
        <input type="text" name="search" placeholder="Search here.." onChange={handlechange} />
        </div>
        <br/>
        <Find name={entry}/>
        </>
    )
}
export default Search;