const Note=(props)=>{
    return(
        <>
    <div className="col-lg-4 col-xs-12 mb-2">
      <div className="card Regular shadow">
  <div className="card-body">
    <h4 className="card-title">{props.title}</h4>
    <p className="card-text" style={{fontSize:"13px"}}>{props.content}</p>
    <button className="btn btn-danger" onClick={()=>props.deletenote(props.id)}>Delete</button>
  </div>
</div>
    </div>
</>
    )
}
export default Note;