import { useState } from 'react'
import Createnote from './Createnote'
import Note from './Note'
import './notes.css'


//const arr = [];
const Mainnote=()=>{
    const [newnotes, setnewnotes] = useState([]);

    const makenotes=(note) => {
      setnewnotes((preData) => {
          return [
            ...preData,
             note,
            ];
        }) 
    }

    const ondelete=(id)=>{
      alert("Do you really want to delete this note..!");
      setnewnotes((oldvalues)=>{
          return oldvalues.filter((arritem,index)=>{  //Filtering in React is pretty much what it says it is.
              return index !== id;                    //It’s the process of looping through an array and including or excluding elements inside that array based on a condition that you provide.
          });                                         //The caveat here is that we’re not actually filtering using React.
      })                                              // Filter is a JavaScript function that we can perform on an array type object. Remember, we use JavaScript to write React.
  }

    return(
        <div>
        <section className='createnote' >
          <Createnote newnote={makenotes} />
        </section>
        <section className='notes'>
        <div className="container">
  <div className="row">
          {/* <Note/> */}

          {newnotes.map((val,index) => {
               return (      //map method can take 4 args i.e. current value, index, arrayname, this.value
            <Note
              key= {index}
              id = {index}
              title={val.title}
              content={val.content}
              deletenote={ondelete}/>
              )
          })
        }
        </div>
        </div>
        </section>
        </div>
    )
}
export default Mainnote;