import { useReducer, dispatch } from "react";


// const ans = {

    
    
    const Usereducer=()=>{
        // state is the state we want to show in the UI.
        
        const reducer=(state, action)=>{
            switch(action.type) {
                case 'increment':
                    return state+1;
                case 'decrement':
                    return state-1;
                default:
                    throw new Error();
            }
        }

        const [state, dispatch] = useReducer(reducer, 0);
        
        return (
            <>
            <div className="mybody">
                <h1 className="text">This is Use of <span>U</span>se<span>R</span>educer Hook.</h1>
                <hr/>
            <h3>Count : {state}</h3>
            <button className="btn" onClick={() => dispatch({type:'decrement'})}>-</button>
            <button className="btn" onClick={() => dispatch({type:'increment'})}>+</button>
            </div>
            </>
        )
    }

    export default Usereducer;


