import { useState } from "react";

const Createnote=(props)=>{

    const [expand, setexpand] = useState(false);

    const [note,setnote] = useState({
        title:"",
        content:"",
    })

    const expandon = () =>{
        setexpand(true);
    }
    const expandoff = () =>{
        setexpand(false);
    }
    const noteentry=(e)=>{
        e.preventDefault();
        const { name, value } = e.target;
        setnote((olddata)=>{
            return {
                ...olddata,
                [name]: value,
            };
        });
        console.log(note);
    };
    const makenotes=()=>{
        props.newnote(note);
        setnote({
            title:"",
            content:"",
        })
    }
    return(
        <>
        <div className="cnote"  onDoubleClick={expandoff}>
            <h1>Make Your Notes</h1>
            
            {expand  && <><input name="title" placeholder="Enter Title" value={note.title} onChange={noteentry}/><br/></>}
            
            <textarea rows="2" name="content" placeholder="Write Notes here..." value={note.content} onChange={noteentry} onClick={expandon}/><br/>
            
            {expand  &&<button className="btn btn-primary" style={{fontSize:"14px"}} onClick={makenotes}>Submit</button>}
            
        </div>
        </>
    )
}
export default Createnote;