import { useState } from "react";

const SecondF=()=>{

    let user = "";

    const [entry,setentry] = useState();
    const [uname,setuname] = useState(user);
    const [pw,setpw] = useState();
    //const [pas,setpas] = useState();
    const pwd = "password";

    const username=(e)=>{
        //e.preventDefault();
        setentry(e.target.value);
    }

    const pass=(e)=>{
        setpw(e.target.value);
    }
    const submit=(e)=>{
        e.preventDefault();
        if(pw===pwd){
            setuname(entry);
            //setpas(pw);
        document.getElementById('wel').style.display = "block";
        }
        else{
            alert("🛑 Password Not Matching 🛑");
            window.location.reload();
        }
    }

    return(
        <>
        <div className="mybody">
            <h1 className="text">This is a Login form</h1>
            <hr/>
            <form onSubmit={submit}>
            <input type="text" placeholder="UserName" onChange={username}></input><br/>
            <input type="password" placeholder="Password" onChange={pass}></input><br/>
            <button className="btn" type="submit" > Login..! ✅ </button>
            </form>

            <hr/><hr/>
            <div id="wel" style={{display:"none"}}>
                <h1>Welcome, {uname}</h1>
            </div>

        </div>
        </>

    );

}
export default SecondF;