import { useState } from "react";

const Myaccordian=({Ques,Ans})=>{
     const [state,setstate] = useState(false);

    return(
        <>
        <div className="accordian col-lg-12">
            <div className="container">
            <div className="row">
                <div className="col-lg-8 col-xs-10">
                   <h2>{Ques}</h2>
                </div>
                <div style={{textAlign:"right"}} className="col-lg-4 col-xs-2">
                   <span style={{fontSize:"12px", margin:"0px 5px"}} onClick={()=>{setstate(!state)}}>{state ? '➖' : '➕' }</span>
                </div>
           </div>
           </div>
         </div>
         {state && <h4 className="panel">{Ans}</h4>}
        </>
    )
}
export default Myaccordian;