import { useState } from "react";

const ThirdF=()=>{

    const [entry,setentry] = useState({
        uname:"",
        email:"",
        phone:"",
    }); // we can pass object in the usestate like this.

    const Setinput=(e)=>{
        //const value = e.target.value;
        //const name = e.target.name;
        //console.log(e.target.placeholder);
        const {value, name} = e.target;
        setentry((preValue)=>{
            if(name==="uname"){
                return{
                    uname: value,
                    email: preValue.email,
                    phone: preValue.phone,
                }
            }
            if(name==="email"){
                return{
                    uname: preValue.uname,
                    email: value,
                    phone: preValue.phone,
                }
            }
            if(name==="phone"){
                return{
                    uname: preValue.uname,
                    email: preValue.email,
                    phone: value,
                }
            }
        })
    }
    const submit=(e)=>{
        e.preventDefault();
        alert("Data Submitted Successfully.. ✔️");
        document.getElementById("wel").style.display="block";
    }
    return(
        <>
        <div className="mybody">
            <h1 className="text">This is a Login form</h1>
            <hr/>

            <div id="wel" style={{display:"none"}}>
                <h1>Welcome, {entry.uname} {entry.email} {entry.phone} </h1>
            </div>

            <form onSubmit={submit}>
            <input type="text" name="uname" placeholder="UserName" onChange={Setinput} required="true" autoComplete="off"></input><br/>
            <input type="text" name="email" placeholder="Email" onChange={Setinput} required="true" autoComplete="off"></input><br/>
            <input type="number" name="phone" placeholder="Number" onChange={Setinput} required="true" autoComplete="off"></input><br/>
            <button className="btn" type="submit" > Login..! ✅ </button>
            </form>
            <hr/><hr/>

        </div>
        </>

    );

}
export default ThirdF;