import { useEffect, useState } from 'react';
import './table.css'

const Table1=()=>{
    const [data,setdata] = useState([]);

    const url = "https://api.covid19india.org/data.json";
    //var headers = {};
    const getdata = async() => {
        const res = await fetch(url
        );
        const realdata = await res.json();
        console.log(realdata.statewise);
        setdata(realdata.statewise);
    }

    useEffect(()=>{
        getdata();
    },[]);

    return(
        <>
        <div className="mybody">
            {/* <h1>This is Table</h1> */}
            <br/>
            <div className='container-fluid'>
            <div className='main-heading'>
            <h1>This is Table1 Using Bootstrap Showing COVID Data</h1>
            </div>
            <div className="table-responsive" style={{margin:"5px 25px 5px 25px"}}>
             <table className="table table-hover">
                 <thead className="table-dark">
                     <tr>
                         <th>State Name</th>
                         <th>Active</th>
                         <th>Confirmed</th>
                         <th>Deaths</th>
                         <th>Recovered</th>
                         <th>Last Updated</th>
                     </tr>
                 </thead>
                 <tbody>
                    {
                     data.map((val,index)=>{
                         return(
                    <tr key={index}>
                         <td>{val.state}</td>
                         <td>{val.active}</td>
                         <td>{val.confirmed}</td>
                         <td>{val.deaths}</td>
                         <td>{val.recovered}</td>
                         <td>{val.lastupdatedtime}</td>
                    </tr>        
                         )
                     })
                    }
                     {/* <tr>
                         <td>Content1</td>
                         <td>Content2</td>
                         <td>Content3</td>
                         <td>Content4</td>
                         <td>Content5</td>
                         <td>Content6</td>
                     </tr>
                     <tr>
                         <td>Content1</td>
                         <td>Content2</td>
                         <td>Content3</td>
                         <td>Content4</td>
                         <td>Content5</td>
                         <td>Content6</td>
                     </tr>
                     <tr>
                         <td>Content1</td>
                         <td>Content2</td>
                         <td>Content3</td>
                         <td>Content4</td>
                         <td>Content5</td>
                         <td>Content6</td>
                     </tr> */}
                 </tbody>
             </table>
            </div>
            </div>
        </div>
        </>

    )
}

export default Table1;